import React from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { Container, Grid, Typography } from '@material-ui/core'
import { makeStyles } from '@material-ui/core/styles'

import ArtworkCard from '../../components/Card/ArtworkCard'
import CollectionCard from '../../components/Card/CollectionCard'
import CreatorCard from '../../components/Card/CreatorCard'

import { selectLiveArtworks } from '../../store/artwork/artworkSlice'

import './home.css'

const Home = () => {
  const artworks = useSelector((state) => selectLiveArtworks(state))
  const creators = useSelector((state) => state.creator.creators)
  let collection = [...artworks]
  collection = collection.sort(() => Math.random() - Math.random()).slice(0, 4)

  return (
    <div className="root home-page ">
      <div className="container">
        
        <div>
          <div className="card-pack">
            {collection &&
              collection.map((artwork) => {
                return <CollectionCard key={artwork._id} artwork={artwork} />
              })}
          </div>
        </div>

        <div>
          <div className="home-creators">
            <h1>Trending Creators</h1>

            <div className="creators-galllary">
              {creators &&
                creators.map((creator, i) => {
                  if (i < 6) {
                    return <CreatorCard key={creator._id} creator={creator} />
                  }
                })}
            </div>
          </div>
        </div>

        <div>
          <div className="home-artworks">
            <h1>Hot Artworks</h1>

            <div className="artworks-galllary">
              {artworks &&
                artworks.map((artwork, i) => {
                  if (i < 8) {
                    return <ArtworkCard key={artwork._id} artwork={artwork} />
                  }
                })}
            </div>
          </div>
        </div>
     
      </div>

      {/* <div>
        <Grid
          direction="row"
          justify="flex-start"
          alignItems="center"
          justify="center"
          container
          className="root"
          spacing={2}
        >
          <Grid item xs={12}>
            <Grid container spacing={3}>
              {artworks &&
                artworks.map((artwork) => {
                  return (
                    <Grid key={artwork._id} item md={3}>
                      <ArtworkCard artwork={artwork} />
                    </Grid>
                  )
                })}
            </Grid>
          </Grid>
        </Grid>
      </div> */}
    </div>
  )
}
export default Home
