import React from 'react'
import { Link } from 'react-router-dom'
import { makeStyles } from '@material-ui/core/styles'
import {isVideo} from '../../utils'
import Clip from '../../components/Clip'


import { IMG_BASE } from '../../constant'
import CoverImg from '../../images/cover.jpg'
import AvatarImg from '../../images/avatar.jpg'

import './index.css'


const CreatorCard = (props) => {

  let {
    _id,
    fullname,
    username,
    bio,
    coverImg,
    avatar,
    publicAddress,
  } = props.creator

  return (
    <Link  to={`/${username ? username : publicAddress}`} className="card card-creator" >
      <div className="media"> 
        {isVideo(coverImg) ? (
                <Clip url={IMG_BASE + coverImg} className="video"  />
              ) : (
                <img src={coverImg? IMG_BASE + coverImg: CoverImg} className="img" />
          )}
      
      </div>
      <div className="card-content">
      {isVideo(avatar) ? (
                <Clip url={IMG_BASE + avatar} className="video"  />
              ) : (
                <img src={avatar? IMG_BASE + avatar: AvatarImg} className="img" />
          )}

                <div className="creator-name">{username}</div>
                <div className="creator-bio">{bio}</div>
        </div>
    </Link>
  )
}

export default CreatorCard
