import React, { useState, useEffect } from 'react'
import { Link } from 'react-router-dom'
import { ReactComponent as SearchIcon } from '../../images/svg/search.svg'

import { IMG_BASE } from '../../constant'
import { artworkAPI } from '../../api/artwork'
import './index.css'

const SearchBar = () => {
  const { searchItems } = artworkAPI

  const [searchValue, setSearchValue] = useState('')
  const [dropdown, setDropdown] = useState(false)
  const [items, setItems] = useState({
    status: 'idle',
    artworks: [],
    creators: [],
  })

  const onSearchHandleChange = (e) => {
    let value = e.target.value
    setSearchValue(value)
    if (value.length > 1) {
      fetchItem()
    }

    if (items.artworks.length > 0 || items.creators.length > 0) {
      setDropdown(true)
    }
  }

  const fetchItem = async () => {
    try {
      if (items.status === 'idle') {
        setItems({ ...items, status: 'fetching' })
        let responose = await searchItems(searchValue)
        setItems({
          status: 'idle',
          artworks: responose.artworks,
          creators: responose.creators,
        })
      }
    } catch (err) {
      console.log('ERROR')
      console.log(err)
    }
  }

  const renderSearchedItems = () => {
    let artworks = items.artworks
    if (artworks.length <= 0) {
      return (<div>no items</div>)
    }
    return artworks.map((artwork) => {
      return (
        <Link
          to={`/${artwork.username}/${artwork.url}`}
          key={artwork._id}
          className="item"
        >
          <img src={IMG_BASE + artwork.artwork} />
          <div className="item-content">
            <div className="bold-text">{artwork.title}</div>
            <div className="faded-text">{artwork.price / 1e18} BNB</div>
          </div>
        </Link>
      )
    })
  }

  const renderSearchedCreators = () => {
    let creators = items.creators
    if (creators.length <= 0) {
      return (<div>no users</div>)
    }
    return creators.map((creator) => {
      return (
        <Link
          to={`/${creator.username ? creator.username : creator.publicAddress}`}
          key={creator._id}
          className="item"
        >
          <img src={IMG_BASE + creator.avatar} />
          <div className="item-content">
            <div className="bold-text">{creator.username}</div>
            {/* <div className="faded-text">{creator.price / 1e18} ETH</div> */}
          </div>
        </Link>
      )
    })
  }

  return (
    <div className="search-container">
      <div className="inside-container" aria-expanded="false">
        <form className="">
          <div className="search-icon-container">
            <span className="">
              <div className="search-icon">
                <SearchIcon />
              </div>
            </span>
          </div>
          <div id="search-input" className="">
            <input
              placeholder="Search by creator, collectible"
              autoCapitalize="sentences"
              autoComplete="on"
              autoCorrect="on"
              dir="auto"
              enterKeyHint="search"
              spellCheck="true"
              type="search"
              className="search-input"
              value={searchValue}
              onChange={(e) => onSearchHandleChange(e)}
            />
          </div>
          <input
            type="submit"
            hidden=""
            className="search-btn"
            value="Search"
          />
        </form>
      </div>

      {dropdown ? (
        <>
          <div className="search-dropdown">
            <div className="search-dropdown-container">
              <div className="item-container">
                <div className="item-title">Items</div>
                {renderSearchedItems()}
              </div>
             
              <div className="item-container">
                <div className="item-title">Users</div>
                {renderSearchedCreators()}
              </div>

            </div>
          </div>
          <div
            onClick={() => {
              setDropdown(false)
            }}
            className="backdoor"
          ></div>
        </>
      ) : (
        <div></div>
      )}
    </div>
  )
}

export default SearchBar
