

export const artworkAPI = {}
const axios = require('axios').default
axios.defaults.headers.post['Content-Type'] = 'application/json';

artworkAPI.fetchAllArtwork = async () => {
    return new Promise(async (resolve, reject) => {
        try {
            let response = await axios(`/api/artwork/`)
            resolve(response.data)
            return
        } catch (err) {
            console.log(err)
            return reject(err.message ? err.message : 'internal error')
            
        }
    })
}
artworkAPI.fetchArtwork = async (tokenId) => {
    return new Promise(async (resolve, reject) => {
        try {
            let response = await axios(`/api/artwork/${tokenId}`)
            resolve(response.data)
            return
        } catch (err) {
            console.log(err)
            return reject(err.message ? err.message : 'internal error')
            
        }
    })
}

